import React,{useState,useEffect} from 'react'
import { Link, useNavigate } from 'react-router-dom';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import LinearProgress from '@mui/material/LinearProgress';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Slider } from '@mui/material';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import CircularProgress from '@mui/material/CircularProgress'
import { server_home } from '../secret/secret';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import TextField from '@mui/material/TextField';
import DialogTitle from '@mui/material/DialogTitle';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import IconButton from '@mui/material/IconButton';
import Avatar from '@mui/material/Avatar';

const theme = createTheme();

class Reward{
    constructor(type,amount,id,created){
      this.type = type; // 0 - created  1 - received
      this.amount = amount;
      this.id = id;
      this.created = created;
    }
};

function Home() {
    const [showAlert,setShowAlert] = useState(false);
    const [rewardAmount,setRewardAmount] = useState(0);
    const [myRewards,setMyRewards] = useState([]);
    const [score,setScore] = useState(0);
    const navigate = useNavigate();

    const handleAlertClose = (event,reason) => {
        if (reason && reason == "backdropClick") return;
        setShowAlert(false);
        navigate('/login');
    };

    async function tryLogin(email,password){
        const response = await fetch(`${server_home}/user/login/`,{
            method:'POST',
            headers:{
                'Content-Type':'application/json',
            },
            body:JSON.stringify({
                officerId:email,
                password:password
            }),
            credentials:'include',
        })
        const data = response.json();
        data.then(res=>{
            console.log(res);
            if (res.status==="ok"){
                localStorage.setItem('email',res.data.email);
                localStorage.setItem('password',res.data.password);
                //getMyRewardss();
            }else{
                setShowAlert(true);
            }
        })
      };
    
    async function createRewardClicked(){
        const response = await fetch(`${server_home}/user/createreward`,{
            method:'POST',
            headers:{
                'Content-Type':'application/json',
            },
            body:JSON.stringify({
                amount:rewardAmount
              }),
            credentials: 'include'
        })
        const data = response.json();
        data.then(res=>{
            console.log(res);
            if (res.status=="ok"){
                setRewardAmount(0);
                getMyRewards(); // fetch the rewards again
            }else{
                
            }
        })
    }
    async function getMyRewards(){
        const response = await fetch(`${server_home}/user/myprofile`,{
            method:'GET',
            headers:{
                'Content-Type':'application/json',
            },
            credentials: 'include'
        })
        const data =response.json();
        //console.log(data);
        data.then(res=>{
            console.log(res);
            if (res.status=="ok"){
                setScore(res.data.rewards);
                let newArr = new Array();
                let createdRewards = res.data.rewardsCreated;
                let receivedRewards = res.data.rewardsReceived;
                console.log("created rewards: ",createdRewards);
                console.log("received rewards",receivedRewards);
                for (let key in createdRewards){
                    const rev = new Reward(0,createdRewards[key]['amount'],createdRewards[key]['_id'],createdRewards[key]['created']);
                    newArr.push(rev);
                }
                for (let key in receivedRewards){
                    const rev = new Reward(1,receivedRewards[key]['reward']['amount'],receivedRewards[key]['reward']['_id'],receivedRewards[key]['reward']['created']);
                    newArr.push(rev);
                }
                setMyRewards(newArr);
                console.log("new arr: ",newArr);
            }
            else{
                //console.log("yo");
                let my_email = localStorage.getItem('email');
                let my_password = localStorage.getItem('password');
                tryLogin(my_email,my_password);
            }
        })
    }
    async function logoutClicked(){
        const response = await fetch(`${server_home}/user/logout`,{
          method:'GET',
          headers:{
              'Content-Type':'application/json',
          },
          credentials: 'include'
        })
        const data = response.json();
        data.then(res=>{
          //console.log(res);
          localStorage.removeItem('email');
          localStorage.removeItem('password');
          navigate('/login');
        })
    }
    useEffect(()=>{
        getMyRewards();
    },[])    
    return (
        <ThemeProvider theme={theme}>
      <Dialog
        fullWidth={true}
        open={showAlert}
        onClose={handleAlertClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Please Login!
        </DialogTitle>
        <DialogActions>
          <Button onClick={handleAlertClose}>Login</Button>
        </DialogActions>
      </Dialog>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h7" component="div" sx={{ flexGrow: 1 }}>Referal Portal</Typography>
          <Typography variant='h7' component="div" sx={{marginRight:"20px"}}>{score}</Typography>
          <Button onClick={logoutClicked} variant="contained" color="secondary" sx={{color:"white"}}>Logout</Button>
        </Toolbar>
      </AppBar>
      <Box display="flex" justifyContent="center">
        <TextField
            sx={{margin:"30px"}}
            onChange={(e)=>setRewardAmount(e.target.value)}
            value={rewardAmount || ""}
            label="Reward Amount"
        />
      </Box>
      <Box display="flex" justifyContent="center">
        
        <Button variant="contained" onClick={createRewardClicked} sx={{marginBottom:"50px"}} endIcon={<AddIcon />}>
          <Typography variant="h7" component="div" sx={{ margin:"10px" }}>Create Reward</Typography>
        </Button>
      </Box>
      
      <Box display="flex" justifyContent="center" alignItem="center">
        <Typography variant="h6">Rewards History:</Typography>
      </Box>
      <Container component="main" maxWidth="md">
        <Box sx={{marginTop:3,display: 'flex',flexDirection: 'column',alignItems: 'center',}}>
            {
                myRewards.map((reward_obj,idx)=>{
                    return (
                        reward_obj.type===0 ? (
                            <ListItem sx={{ boxShadow: 2,marginTop:"10px"}}
                                secondaryAction={
                                    <div>
                                        <IconButton sx={{marginRight:"10px"}} title='Amount' edge="end">
                                            <Typography >{reward_obj.amount}</Typography>
                                        </IconButton>
                                        <IconButton title='Share Reward' onClick={()=>{navigator.clipboard.writeText(`https://referal-portal.herokuapp.com/reward/${reward_obj.id}`)}} edge="end">
                                            <ContentCopyIcon sx={{ padding:"10px" }}/>
                                        </IconButton>
                                    </div>
                                }
                            >
                                <ListItemText primary={"Reward Created"}/>
                            </ListItem>
                        ) : (
                            <ListItem sx={{ boxShadow: 2,marginTop:"10px"}}
                                secondaryAction={
                                    <div>
                                        <IconButton sx={{marginRight:"20px"}} title='Amount' edge="end">
                                            <Typography >{reward_obj.amount}</Typography>
                                        </IconButton>
                                    </div>
                                }
                            >
                                <ListItemText primary={"Reward Received"}/>
                            </ListItem>
                        )
                    )
                })
            }
        </Box>
        <br/><br/><br/><br/><br/>
      </Container>
    </ThemeProvider>
    )
}

export default Home
