import React, { useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { server_home } from '../secret/secret';
import { Link, useNavigate } from 'react-router-dom';

function Reward() {
    let {rewardid} = useParams(); // quiz id
    const navigate = useNavigate();

    async function receiveReward(tempstr){
        const req_str=`${server_home}/user/receive/${tempstr}`;
        //console.log(req_str);
        const response = await fetch(req_str,{
            method:'GET',
            headers:{
                'Content-Type':'application/json',
            },
            credentials:'include',
        })
        const data = response.json();
        data.then(res=>{
            console.log(res);
            navigate('/');
        })
    }
    useEffect(()=>{
        console.log(rewardid);
        receiveReward(rewardid);
    })
    return (
        <div>Receiving Reward Wait</div>
    )
}

export default Reward